.c-panel-edit {
    visibility: hidden;
    opacity: 0;
    transition: opacity 1500ms;
}
.c-panel-delete {
    visibility: hidden;
    opacity: 0;
    color: #06b6d4;
    transition: opacity 1500ms;
}

.c-panel-item:hover .c-panel-edit {
    visibility: visible;
    opacity: 1;
}

.c-panel-item:hover .c-panel-delete {
    visibility: visible;
    opacity: 1;
}
.c-panel-paginator,.p-paginator-bottom {
    height: 10%;
    border: none;
}